


































































export default {
	data() {
		return {
			basicsList: [{
				cuIcon: 'usefullfill',
				name: '开始'
			}, {
				cuIcon: 'radioboxfill',
				name: '等待'
			}, {
				cuIcon: 'roundclosefill',
				name: '错误'
			}, {
				cuIcon: 'roundcheckfill',
				name: '完成'
			}, ],
			basics: 0,
			numList: [{
				name: '开始'
			}, {
				name: '等待'
			}, {
				name: '错误'
			}, {
				name: '完成'
			}, ],
			num: 0,
			scroll: 0
		};
	},
	methods: {
		BasicsSteps() {
			this.basics= this.basics == this.basicsList.length - 1 ? 0 : this.basics + 1				
		},
		NumSteps() {
			this.num= this.num == this.numList.length - 1 ? 0 : this.num + 1				
		},
		ScrollSteps() {
			this.scroll= this.scroll == 9 ? 0 : this.scroll + 1				
		}
	}
}
